.list{
    display: flex;
    position: absolute;
    left: 0%;
    top: 100%;

    max-width: 150px;
    overflow-x: auto;
    padding: 0 5px;
    padding-top: 6px;
    background-color: rgb(231, 231, 231);
    box-shadow: 0px 7px 9px 1px rgb(0 0 0 / 60%);
}
.wraper{
    position: relative;
}
.text{
    background-color: red;
    cursor: pointer;
    transition: 300ms;
}
.text:hover{
    background-color: rgb(255, 70, 70);
}
.rules{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.rule{
    width: 100%;
    text-align: center;
    background-color: transparent;
    color:black;
    transition: 300ms;
    cursor: pointer;
    padding: 0 5px;
}
.rule:hover{
    color:white;
    background-color: #369cef;
}
.title{
    text-align: center;
    margin: 0 10px;
    margin-bottom: 5px;
    border-bottom: solid 1px black;
}