.btn{
    cursor: pointer;
    border: none;
    position: relative;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #ef3636;
    margin-left: 10px;
}

.span{
    display: block;
    position: absolute;
    background-color: white;
    height: 3px;
    width: 18px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    
}
